<!-- *************************************************************************
     TEMPLATE
     ************************************************************************* -->

<template lang="pug">
button(
  @click="onClick"
  :class=`[
    "c-instagram-button",
    "c-instagram-button--" + size,
    {
      "c-instagram-button--full-width": fullWidth
    }
  ]`
)
  span.c-instagram-button__inner
    slot(
      v-if="$slots.default && $slots.default[0].text.trim()"
    )

    template(
      v-else
    ) Follow us on Instagram
</template>

<!-- *************************************************************************
     SCRIPT
     ************************************************************************* -->

<script>
// PROJECT: MIXIN
import SizeMixin from "@/mixins/main/SizeMixin"

export default {
  mixins: [SizeMixin],

  props: {
    fullWidth: {
      type: Boolean,
      default: false
    }
  },

  methods: {
    // --> EVENT LISTENERS <--

    onClick(event) {
      this.$emit("click", event)
    }
  }
}
</script>

