<!-- *************************************************************************
     TEMPLATE
     ************************************************************************* -->

<template lang="pug">
div(
  :class=`[
    "c-widget-header-content",
    "c-widget-header-content--" + size,
    "c-widget-header-content--" + theme
  ]`
)
  img(
    v-if="avatar"
    @click="onAvatarClick"
    :src="avatar"
    class="c-widget-header-content__avatar"
  )

  .c-widget-header-content__information
    span(
      v-if="name"
      @click="onNameClick"
      class="c-widget-header-content__name"
    ) {{ name }}

    slot(
      name="button"
    )
</template>

<!-- *************************************************************************
     SCRIPT
     ************************************************************************* -->

<script>
// PROJECT: MIXINS
import SizeMixin from "@/mixins/main/SizeMixin"
import ThemeMixin from "@/mixins/main/ThemeMixin"

export default {
  mixins: [SizeMixin, ThemeMixin],

  props: {
    avatar: {
      type: String,
      default: null
    },
    name: {
      type: String,
      default: null
    }
  },

  methods: {
    // --> EVENT LISTENERS <--

    onAvatarClick() {
      this.$emit("avatarClick")
    },

    onNameClick() {
      this.$emit("nameClick")
    }
  }
}
</script>

<!-- *************************************************************************
     STYLE
     ************************************************************************* -->

<style lang="scss">
// VARIABLES
$c: ".c-widget-header-content";
$sizes: "mini", "small", "default", "medium", "large";

#{$c} {
  display: flex;

  #{$c}__avatar {
    flex: 0 0 auto;
    margin-top: 2px;
    border-radius: 100%;
    cursor: pointer;
  }

  #{$c}__information {
    display: flex;
    align-items: flex-start;
    flex: 1;
    flex-direction: column;

    #{$c}__name {
      font-weight: 800;
      cursor: pointer;

      &:hover {
        text-decoration: underline;
      }
    }
  }

  // --> SIZES <--

  @each $size in $sizes {
    $i: index($sizes, $size) - 1;
    &--#{$size} {
      #{$c}__avatar {
        margin-right: 10px + (1px * $i);
        width: 40px + (5px * $i);
        height: 40px + (5px * $i);
      }

      #{$c}__information {
        #{$c}__name {
          margin-bottom: 6px + (1px * $i);
          font-size: 12px + (1px * $i);
          line-height: 12px + (1px * $i);
        }
      }
    }
  }

  // --> THEMES <--

  @each $theme in $themes {
    $themeName: map-get($theme, "name");

    &--#{$themeName} {
      #{$c}__avatar {
        box-shadow: 0 1px 3px mdg($theme, "shadows", "default", "primary");
      }
    }
  }
}
</style>
