<!-- *************************************************************************
     TEMPLATE
     ************************************************************************* -->

<template lang="pug">
component(
  v-bind="$props"
  @close="onClose"
  :class=`[
    "c-instagram-post",
    "c-instagram-post--" + layout,
    "c-instagram-post--" + size,
    "c-instagram-post--" + theme,
    {
      "c-instagram-post--loading": loading
    }
  ]`
  :is="layout + '-layout'"
  :is-closed="closed"
  :is-triggered="triggered"
  :transition="transition"
  direction="vertical"
  popover-size="medium"
)
  widget-header(
    :size="size"
    :theme="theme"
    slot="header"
  )
    widget-header-content(
      @avatarClick="onGoToInstagram"
      @nameClick="onGoToInstagram"
      :avatar="instagramAvatar"
      :name="instagramName || instagramUsername"
      :size="size"
      :theme="theme"
      class="c-instagram-post__container"
    )
      instagram-button(
        @click="onGoToInstagram"
        :size="size"
        slot="button"
      ) {{ buttonContent }}

  div(
    v-if="instagramPostUrl"
    class="c-instagram-post__body c-instagram-post__container"
    slot="body"
  )
    base-spinner(
      v-if="loading"
      :size="size"
      class="c-instagram-post__spinner"
    )

    div(
      v-show="!loading"
      v-html="oEmbedHtml"
      class="c-instagram-post__post"
    )
</template>

<!-- *************************************************************************
     SCRIPT
     ************************************************************************* -->

<script>
// NPM
import debounce from "lodash/debounce"
import axios from "axios"
import { BaseSpinner } from "@growthbunker/vuedarkmode"

// PROJECT: COMPONENTS
import InstagramButton from "@/components/buttons/InstagramButton"
import WidgetHeader from "@/components/common/WidgetHeader"
import WidgetHeaderContent from "@/components/common/WidgetHeaderContent"

// PROJECT: HELPERS
import { loadScript } from "@/helpers/widgets"

// PROJECT: MIXINS
import CloseMixin from "@/mixins/main/CloseMixin"
import EditorMixin from "@/mixins/main/EditorMixin"
import LayoutMixin from "@/mixins/main/LayoutMixin"
import PositionMixin from "@/mixins/main/PositionMixin"
import SizeMixin from "@/mixins/main/SizeMixin"
import SubscriptionMixin from "@/mixins/main/SubscriptionMixin"
import ThemeMixin from "@/mixins/main/ThemeMixin"
import TransitionMixin from "@/mixins/main/TransitionMixin"
import TriggerMixin from "@/mixins/main/TriggerMixin"
import WidgetMixin from "@/mixins/main/WidgetMixin"

export default {
  name: "InstagramPost",

  components: {
    BaseSpinner,
    InstagramButton,
    WidgetHeader,
    WidgetHeaderContent
  },

  mixins: [
    CloseMixin,
    EditorMixin,
    LayoutMixin,
    PositionMixin,
    SizeMixin,
    ThemeMixin,
    SubscriptionMixin,
    TransitionMixin,
    TriggerMixin,
    WidgetMixin
  ],

  props: {
    buttonContent: {
      type: String,
      default: null
    },
    instagramAvatar: {
      type: String,
      default: null
    },
    instagramName: {
      type: String,
      default: null
    },
    instagramPostUrl: {
      type: String,
      default: null
    },
    instagramUsername: {
      type: String,
      required: true
    }
  },

  data: () => ({
    // --> STATE <--

    loading: false,
    oEmbedHtml: null
  }),

  watch: {
    layout: {
      handler() {
        this.loadPost()
      }
    },
    instagramPostUrl: {
      handler: debounce(function() {
        this.loadPost()
      }, 500)
    }
  },

  mounted() {
    this.loadPost()
  },

  methods: {
    // --> HELPERS <--

    async loadPost() {
      if (this.instagramPostUrl) {
        const endpoint = `https://api.instagram.com/oembed?url=${this.instagramPostUrl}`

        this.loading = true

        if (!window.instgrm) {
          await loadScript("https://www.instagram.com/embed.js")
        }

        try {
          const response = await axios.get(endpoint)

          this.oEmbedHtml = response.data.html

          this.$nextTick(() => {
            window.instgrm.Embeds.process()
          })
        } catch (error) {
          console.error(error)
        }

        this.loading = false

        // Hide spinner when widget is loaded
        // window.twttr.events.bind("rendered", () => {
        //   this.loading = false
        // })
      }
    },

    // --> EVENT LISTENERS <--

    onGoToInstagram() {
      window.open(
        `https://www.instagram.com/${this.instagramUsername}`,
        "_blank"
      )
    }
  }
}
</script>

<!-- *************************************************************************
     STYLE
     ************************************************************************* -->

<style lang="scss">
