#{$c} {
  #{$c}__container {
    margin: 0 auto;
    max-width: 600px;
  }

  #{$c}__body {
    overflow-y: scroll;

    #{$c}__post {
      display: flex;
      justify-content: center;

      .instagram-media {
        flex: 0 0 auto !important;
        margin: 0 !important;
        width: 100% !important;
      }
    }
  }

  // --> LAYOUTS <--

  &--banner {
    #{$c}__body {
      max-height: 600px;
    }
  }

  &--popup {
    #{$c}__body {
      max-height: 500px;
    }
  }

  &--popover {
    #{$c}__body {
      max-height: 430px;
    }
  }

  // --> SIZES <--

  @each $size in $sizes {
    $i: index($sizes, $size) - 1;

    &--#{$size} {
      #{$c}__body {
        padding: 8px + (1px * $i);
      }

      &#{$c}--loading {
        #{$c}__body {
          padding: 40px + (5px * $i) 0;
        }
      }
    }
  }

  // --> BOOLEANS <--

  &--loading {
    #{$c}__body {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}
</style>
